<template>
  <div class="container">
      <el-row :gutter="20">
          <el-col :xs="12" :sm="12" :md="8" :lg="8" v-for="(item,index) in msData" :key="index">
              <div class="item">
                  <img  :src="item.url">
                  <span>{{item.des}}</span>
              </div>
          </el-col>
      </el-row>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'ms',
    data() {
        return {
            msData: []
        }
    },
    async created() {
        let res = await axios.get('/api/ms')
        this.msData = res.data.data
    }
}
</script>

<style lang="scss" scoped>
.item{
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    margin-bottom: 30px;
        background-color: lightgray;
    img{
        border-radius: 20px;
        height: 200px;
    }span{
        height: 40px;
        line-height: 40px;
        text-align: center;

    }

}
</style>



