import Vue from 'vue'
import Router from 'vue-router'
import nzfj from '../components/nzfj'
import ms from '../components/ms'
import zqyz from '../components/zqyz'
import yl from '../components/yl'
import dt from '../components/dt'
Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'nzfj',
            component: nzfj
        },

        {
            path: '/nzfj',
            name: 'nzfj',
            component: nzfj
        },
        {
            path: '/zqyz',
            name: 'zqyz',
            component: zqyz
        },
        {
            path: '/ms',
            name: 'ms',
            component: ms
        },
        {
            path: '/yl',
            name: 'yl',
            component: yl
        },
        {
            path: '/dt',
            name: 'dt',
            component: dt
        }
    ]
})



